@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
/* variaveis */
:root {
  --main-color: #22223b;
  --secundary-color: #ecf5f9;
}

/* geral */
* {
  margin: 0;
  padding: 0;
  line-height: 1.5;
  box-sizing: border-box;
  border: 0;
  text-decoration: none;
  list-style: none;
}

html,
body {
  height: 100%;
}

html {
  background-color: #ecf5f9;
  background-color: var(--secundary-color);
  color: #22223b;
  color: var(--main-color);
  font-family: "Nunito", sans-serif;
  font-size: 20px;
}

body {
  max-width: 1200px;
  max-height: 2000px;
  margin: 0 auto;
}


#root {
  height: 100%;
  display: flex;
  flex-direction: column;
}

a,
span {
  font-size: 1rem;
  color: #22223b;
  color: var(--main-color);
}

label {
  font-size: 0.8rem;
}

main {
  flex-grow: 1;
}

/* classes reutilizaveis */
.centralizar-xy {
  display: flex;
  justify-content: center;
  align-items: center;
}

.centralizar-x {
  margin: 0 auto;
}

.centralizar-y {
  flex-direction: column;
}

.subtitulo {
  font-size: 1.2rem;
  margin-top: 30px;
  margin-bottom: 50px;
  text-align: center;
}

.ancora {
  color: #22223b;
  color: var(--main-color);
  cursor: pointer;
}

.ancora:hover {
  border-bottom: 2px solid #22223b;
  border-bottom: 2px solid var(--main-color);
}

.ancora-ativa {
  font-weight: bold;
}

.formulario {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 0 20px;
}

.input-box {
  background-color: transparent;
  border-bottom: 2px solid #22223b;
  border-bottom: 2px solid var(--main-color);
  width: 70%;
  max-width: 390px;
  font-size: 0.8rem;
  outline: none;
  margin-top: 10px;
  margin-bottom: 40px;
  margin-left: 0;
  display: block;
  text-align: left;
}

.input-box::-webkit-input-placeholder {
  color: #b7c2c5;
}

.input-box:-ms-input-placeholder {
  color: #b7c2c5;
}

.input-box::placeholder {
  color: #b7c2c5;
}

.listagem-container-empresa > div {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 30px;
}

.listagem-container-empresa .botoes-container {
  margin-top: 0;
  margin-left: 30px;
}

.listagem-container-empresa .subtitulo {
  margin-top: 0;
  margin-bottom: 0px;
}

.listagem-container-empresa p {
  margin-left: 30px;
}

.loader {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  display: block;
}

.input-box-error {
  margin-bottom: 0;
}

.input-box-error ~ span {
  color: red;
  display: block;
  margin-top: 10px;
  margin-bottom: 40px;
  font-size: 0.6rem;
}

@media (min-width: 768px) {
  html {
    font-size: 23px;
  }
}

@media (min-width: 1024px) {
  html {
    font-size: 26px;
  }
}

.pagina-inicial-container > .subtitulo {
  text-align: center;
}

.pagina-inicial-container > .formulario {
  display: flex;
  padding: 0;
  align-items: center;
  margin-bottom: 0;
}

.pagina-inicial-container .input-box {
  margin-top: 0;
  margin-bottom: 0;
}

.pagina-inicial-container .botoes-container {
  margin-top: 0;
  margin-left: 30px;
}

.pagina-inicial-chamada {
  font-size: 2rem;
}


.pagina-inicial-fonte-menor {
  font-size: 1rem;
}

.pagina-inicial-fonte-muito-menor {
  font-size: 0.6rem;
}

.pagina-inicial-pula-linha {
  display: block;
}

.cabecalho-container {
  justify-content: space-around;
  padding: 20px;
}

.titulo {
  font-size: 1.5rem;
  margin-right: 20px;
}

.usuario-container {
  display: flex;
  flex-direction: column;
}

.menu-container {
  margin: 15px;
  padding: 10px;
}

.menu-conteudo {
  width: 80%;
  justify-content: space-around;
}

.menu-item {
  font-size: 0.8rem;
}

.menu-flutuante {
  display: inline-block;
  position: relative;
}

.menu-flutuante > .ancora {
  background-color: transparent;
  font-size: 0.8rem;
}

.menu-flutuante-conteudo {
  overflow: auto;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  background-color: var(--secundary-color);
  position: absolute;
  right: 0%;
  border-radius: 2px;
}

.menu-flutuante-conteudo > a,
.menu-flutuante-conteudo > button {
  display: block;
  margin: 30px;
  text-align: center;
  font-size: 0.8rem;
}

.menu-flutuante-conteudo > button {
  background-color: transparent;
}

.botoes-container {
  text-align: center;
  margin-top: 30px;
}

.botao {
  background-color: transparent;
  border: 1px solid var(--main-color);
  color: var(--main-color);
  border-radius: 1px;
  padding: 4px;
  font-size: 0.8rem;
  cursor: pointer;
  border-radius: 2px;
}

.botao-perigo {
  background-color: transparent;
  border: 1px solid #d61f1f;
  color: #d61f1f;
}

.botao-sucesso {
  background-color: transparent;
  border: 1px solid #017a09;
  color: #017a09;
}

.botoes-container > .botao:nth-of-type(2) {
  margin-left: 20px;
}

.botoes-container > .botao:nth-of-type(3) {
  margin-left: 20px;
}

.modal {
  display: block;
  position: fixed;
  padding-top: 20px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  background-color: #00000066;
}

.conteudo-do-modal {
  background-color: var(--secundary-color);
  color: var(--main-color);
  margin: auto;
  padding: 20px;
  width: 80%;
  position: relative;
  border-radius: 10px;
}

.fechar-modal {
  background-color: transparent;
  position: absolute;
  color: #aaaaaa;
  font-size: 28px;
  font-weight: bold;
  top: 5px;
  right: 15px;
}

.fechar-modal:hover,
.fechar-modal:focus {
  color: var(--main-color);
  text-decoration: none;
  cursor: pointer;
}

.entradas-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  margin-top: 20px;
}

.entradas-container > div {
  max-width: 440px;
}

.total-compra {
  font-size: 0.8rem;
  margin-bottom: 30px;
}

.ingresso-container {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  margin-bottom: 70px;
  padding: 0 20px;
}

.evento-nome {
  text-align: center;
}

.ingresso-detalhe-item {
  margin-bottom: 15px;
  font-size: 0.8rem;
}

.ingresso-imagem {
  width: 100%;
  height: 200px;
  max-width: 380px;
  margin: 30px auto;
  display: block;
}


body{
  max-width: 5000px;
}
